import React,{Component} from 'react';

class Forms extends Component{
    constructor(props){
        super(props)
        this.state={
            'firstname':'',
            'lastname':'',
            'email':'',
            'subject':'react'
        }
    }
    
    handleFirstnameChange=(event)=>{
        this.setState({
            firstname:event.target.value
        })
    }
    handleLastnameChange=(event)=>{
        this.setState({
            lastname:event.target.value
        })
    }
    handleemailChange=(event)=>{
        this.setState({
            email:event.target.value
        })
    }
    handlesubjectChange=(event)=>{
        this.setState({
            subject:event.target.value
        })
    }
    handleSubmit=(event)=>{
        alert(`${this.state.firstname} ${this.state.lastname} ${this.state.email} ${this.state.subject}`)
        event.preventDefault()
    }
    render(){
        return(
            <div className="wrapper">
            <div className="form-wrapper">
                <h2>20181CSE0080  Asmitha M</h2>
            <form onSubmit={this.handleSubmit}>
                <h1>REGISTRATION</h1>
            <div className="firstName">
                <label>Firstname</label>
                <input type="text" value={this.state.firstname} onChange={this.handleFirstnameChange}/>
            </div>
            <div className="lastName">
                <label>Lastname</label>
                <input type="text" value={this.state.lastname} onChange={this.handleLastnameChange}/>
            </div>
            <div className="email">
                <lable>Email</lable>
                <input type="email" value={this.state.email} onChange={this.handleemailChange}/>
            </div>
            <div className="subject">
                <lable>Subject</lable>
                <select value={this.state.subject} onChange={this.handlesubjectChange}>
                    <option value='react'>React</option>
                    <option value='angular'>Angular</option>
                    <option value='datamining'>Datamining</option>
                </select>
            </div>
            <div className="submit">
            <button type="submit">Submit</button>
            </div>
            </form>
            </div>
            </div>
        )
    }
}
export default Forms;